export default {
    name: 'SocietyUsers',
    props: ['societyId'],
    data: () => ({
        listEmployees: [],
    }),

    methods: {
        async refreshed() {
            try {
                
        
                let currSocietyId = await this.societyId;
                await this.$http
                .post("/societyUser/bySociety/", {
                    societyId: currSocietyId
                })
                .then(response => {
                    this.listEmployees = response.data.employees;
                })
                .catch(error => console.log(error));
            }
            catch(err) {
                console.log('err');
            }
        }
        
    },

    mounted() {
        this.refreshed();
    },

    updated() {
    }
};