import Customs from "./customService.js";
import router from "../../router";
import SocietyUsers from '../SocietyUsers';
import swal from "sweetalert";

export default {
    name: 'SocietyDetail',
    components: {
        SocietyUsers,
    },
    data: () => ({
        id: "",
        userId: "",
        society: {
            name: "",
            description: ""
        },
        societyLoaded: false
    }),

    methods: {
        navigate() {
            router.go(-1);
        },
        deleteSociety(societyID) {
            this.$http
            .delete("/society/delete/" + societyID).then(response => {
                swal("Success", response.data.message, "success");
                this.navigate();
                return (response);
            });
        },
        getSociety() {
            this.$http
            .post("/society/byId/", {
                id: this.id
            })
            .then(response => {
                if (response.data.society != null) {
                    this.society = response.data.society;
                    this.societyLoaded = true;
                }
                //show "THIS SOCIETY NO EXIST ANYMORE :O"
                
            });
            return null;
        },
        async refreshed() {
            
        }
    },

    created() {
        let user = Customs.getUserDetails();
        this.userId = user._id;
        this.id = this.$route.params.id;
        this.getSociety();
    },
};